"use client"; // Next.js (App Router)에서 클라이언트 컴포넌트로 설정

import { useState } from "react";
import Tesseract from "tesseract.js";
import { createWorker } from "tesseract.js";

const OCR = () => {
  const [image, setImage] = useState<string | null>(null);
  const [text, setText] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(false);

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      console.log(reader);
      reader.onload = () => setImage(reader?.result as string);
      reader.readAsDataURL(file);
    }
  };

  const handleExtractText = async () => {
    if (!image) return;

    setLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 0)); // 로딩 상태 반영을 위한 딜레이

    try {
      const worker = await createWorker("eng");
      const {
        data: { text },
      } = await worker.recognize(data);
      await worker.terminate();
      setText(text);
    } catch (error) {
      console.error("OCR 오류:", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col items-center p-4">
      <input
        type="file"
        accept="image/*"
        onChange={handleImageUpload}
        className="mb-4"
      />
      {image && (
        <img
          src={image}
          alt="Uploaded"
          className="w-64 h-auto mb-4 border rounded"
        />
      )}

      <button
        onClick={handleExtractText}
        disabled={loading}
        className="bg-blue-500 text-black px-4 py-2 rounded hover:bg-blue-600"
      >
        {loading ? "추출 중..." : "텍스트 추출"}
      </button>

      {text && (
        <div className="mt-4 p-2 border rounded bg-gray-100 w-full max-w-lg">
          <h3 className="font-semibold">추출된 텍스트:</h3>
          <p className="whitespace-pre-wrap">{text}</p>
        </div>
      )}
    </div>
  );
};

export default OCR;
